.capabilities {
    .content-page_hero {
        @include content-hero-img(url('/img/capabilities-hero-collage.jpg'));
        >div {
            position: relative;
            top: 45px;

            @include mq(medium) {
                position: static;
                top: inherit;
            }
        }
    }

    &--synopsis {
        display: flex;
        justify-content: space-between;
    }

    .homepage--synopsis_box {
        min-width: 375px;
    }

    &--guarentee {
        display: flex;

        >div {
            &:nth-of-type(2) {}
        }
    }

    .article--callout {
        @include article-callout (url('/img/capabilities/capabilities-callout.jpg'));
    }

    .hr-service {
        margin-top: 30px;
    }
}

.services-icons {
    &_row {
        margin-top: 70px;
        margin-bottom: 70px;
        display: flex;
        align-items: center;
        justify-content: center;

        >div {
            margin-left: 50px;
            margin-right: 50px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
        }
    }

    p {
        margin-top: 15px;
        max-width: 200px;
        text-align: center;
        font-size: 14px;
    }
}